@import "resets.scss";
@import "normalizez.scss";
@import url("https://cdn.jsdelivr.net/npm/slick-carousel@1.8.1/slick/slick.css");

@font-face {
    font-family: "Montserrat";
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: "Montserrat";
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

$color-orange: #B85339;
$color-black: #3F3B38;
$color-white: #E9EBE8;
$color-yellow: #D8AF6D;

* {
    box-sizing: border-box;
    color: $color-black; }

html {
    font-family: "Montserrat";
    font-weight: regular; }

a {
    text-decoration: none; }

.line {
    height: 1px;
    width: 35px;
    background: $color-black;
    margin: 0; }

.container {
    position: relative;
    max-width: 1140px;
    margin: 0 auto; }

.main-section {
    min-height: 800px;
    position: relative;
    &__nav-background {
        position: absolute;
        width: 100%;
        height: 120px;
        background: $color-orange; }
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 120px;
        margin-bottom: 6px; }
    &__header {
        h1 {
            color: $color-white;
            margin: 10px 0 10px 0;
            font-size: 40px;
            font-weight: bold;
            a {
                color: $color-white;
                cursor: pointer; } } }
    &__content-container {
        position: relative;
        margin-bottom: 7px; }
    &__text-box {
        position: absolute;
        top: 135px;
        left: 97px; }
    &__title {
        font-size: 32px;
        font-weight: bold;
        margin-bottom: 5px;
        &-line {
            margin-bottom: 45px; } }
    &__text {
        width: 385px;
        font-size: 16px; }
    &__background {
        width: 100%;
        height: 56%; }
    &__search-form-container {
        display: flex;
        justify-content: center;
        input {
            width: 50%;
            background-color: #E9EBE8;
            border-radius: 4px 0 0 4px;
            border: solid 1px $color-black;
            border-right: none;
            padding-left: 30px; }
        input:focus {
            outline: none; }
        button {
            cursor: pointer;
            border-radius: 0 4px 4px 0;
            border: solid 1px $color-black; } } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row;
        &-item {
            // margin-right: 52px
            transition: 0.3s ease-in-out;
            a {
                position: relative;
                width: 85px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                p {
                    color: $color-white;
                    position: absolute;
                    top: 46px;
                    opacity: 0;
                    transition: opacity 0.3s ease-in-out; } } }
        &-item:hover {
            transform: scale(1.1);
            transform: translateY(-10px);
            a {
                p {
                    opacity: 1; } } }
        &-item:last-child {
            margin-right: 0; } } }

.second-section {
    min-height: 800px;
    margin-bottom: 50px;
    &__text {
        width: 100%;
        height: 200px;
        background: $color-orange;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 15px;
        p {
            color: $color-white;
            font-size: 24px;
            display: inline-block;
            text-align: center;
            margin: 0 97px; } }
    &__cards-box {
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
    &__cards-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-radius: 4px;
        overflow: hidden;
        img {
            margin-bottom: 15px;
            transition: 0.7s ease-in-out; }
        img:hover {
            transform: translateY(25%); }
        p {
            font-size: 24px;
            text-align: center;
            background-color: #fff;
            z-index: 1;
            padding-bottom: 10px;
            span {
                font-weight: bold; } } } }

.cards-item {
    position: relative;
    &__hidden-description {
        display: table-cell;
        vertical-align: bottom;
        width: 75%px;
        height: 200px;
        position: absolute;
        top: 0;
        z-index: -1;
        background: $color-yellow;
        font-size: 16px;
        font-weight: bold;
        color: $color-white;
        text-align: center;
        padding: 15px 10px 0 10px; } }

.third-section {
