        align-items: center;
 }        // padding: 0 15px 0 15px
    &__item {
        /* display: flex */
        /*flex-direction: column */
        /*justify-content: flex-start */
        height: 120px; }
    &__footer-background {
        position: absolute;
        width: 100%;
        height: 245px;
        background: $color-orange; }
    &__logo {
