.sign-up-section {
    background: $color-yellow;
    &__container {
        display: flex;
        padding: 5vh 0 5vh 0; }
    &__right-wrapper {
        width: 50%; }
    &__left-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: #E9EBE8;
        border-radius: 0 4px 4px 0;
        width: 50%;
        p {
            margin-bottom: 13px;
            font-size: 24px;
            span {
                display: block;
                font-size: 32px;
                font-weight: bold;
                margin-bottom: 28px; } }
        input {
            width: 70%;
            background: none;
            border: solid 1px $color-black;
            padding: 10px 15px 10px 15px;
            margin-bottom: 28px; }
        input:focus {
            outline: none; }
        button {
            cursor: pointer;
            width: fit-content;
            border: none;
            border-radius: 4px;
            background: $color-orange;
            color: $color-white;
            padding: 10px 30px 10px 30px;
            margin-bottom: 24px;
            transition: 0.5s ease-in-out; }
        button:last-child {
            background: $color-yellow;
            margin-bottom: 0; }
        button:hover {
            transform: scale(1.1); } } }

.right-wrapper {
    &__img {
        height: 100%;
        width: 100%;
        border-radius: 4px 0 0 4px; } }

.product-card {
    &__container {
        margin-top: 15px;
        margin-bottom: 30px;
        min-height: 800px;
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
    &__right-wrapper {
        width: 47%;
        border-radius: 4px; }
    &__left-wrapper {
        width: 50%;
        background: $color-yellow;
        border-radius: 4px; }
    &__text-box {
        margin: 50px 80px 0 80px; }
    &__img {
        width: 100%; }
    &__header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 20px; }
    &__name {
        font-size: 24px;
        font-weight: bold; }
    &__volume {
        font-size: 14px; }
    &__price {
        font-weight: bold;
        color: $color-orange;
        font-size: 20px;
        span {
            text-decoration: line-through;
            font-size: 16px;
            font-weight: 400;
            color: $color-black; } }
    &__description {
        width: 67%;
        margin-bottom: 20px; }
    &__chose-volume {
        margin-bottom: 30px;
        input[type='radio'] {
            accent-color: $color-black; }
        label {
            font-size: 14px;
            font-weight: bold; } }
    &__buttons {
        button {
            border: none;
            border-radius: 4px;
            padding: 15px 10px;
            font-size: 14px;
            font-weight: bold;
            transition: 0.5s ease-in-out; }
        button:hover {
            cursor: pointer;
            transform: scale(1.1); }
        &--orange {
            color: $color-white;
            background: $color-orange;
            margin-right: 15px; } } }

.spoiler_wrap {
    border-bottom: 1px solid $color-black;
    margin-bottom: 15px;
    &.active .spoiler_content {
        max-height: 500px !important;
        padding-bottom: 25px;
        transition: max-height 0.5s ease, padding-bottom 0.3s ease; }
    &.active .spoiler_title {
        color: $color-orange;
        font-weight: bold;
        transition: color 0.5s ease-in-out; } }

.spoiler_content {
    color: $color-black;
    line-height: 24px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s ease, padding-bottom 0.3s ease; }

.plus-minus-toggle {
    cursor: pointer;
    height: 21px;
    position: absolute;
    width: 21px;
    left: -40px;
    top: 50%;
    z-index: 2; }

.plus-minus-toggle:before,
.plus-minus-toggle:after {
    background: $color-black;
    content: '';
    height: 1px;
    left: 0;
    position: absolute;
    top: 0;
    width: 18px;
    transition: transform 500ms ease; }

.plus-minus-toggle:after {
    transform-origin: center; }

.plus-minus-toggle.collapsed:after {
    transform: rotate(90deg); }

.plus-minus-toggle.collapsed:before {
    transform: rotate(180deg); }

.spoiler_title {
    color: $color-black;
    position: relative;
    cursor: pointer;
    padding: 20px 0;
    transition: color 0.5s ease; }

.another-products {
    &__container {
        min-height: 800px; }
    &__text-box {
        height: 90px;
        line-height: 90px;
        background: $color-orange;
        font-size: 24px;
        color: $color-white;
        text-align: center;
        margin-bottom: 60px; } }

.slidecontainer {
    width: 1080px;
    margin: 0 auto;
    position: relative; }

.slide {
    // margin: 0 10px
    text-align: center; }

.slide img {
    width: auto;
    height: 500px;
    margin-bottom: 15px; }

.slide p {
    font-size: 24px; }

.slick-prev {
    left: -40px;
    right: auto; }

.slick-next {
    right: -40px;
    left: auto; }

.slick-prev,
.slick-next {
    background-color: transparent;
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 50%;
    display: block;
    width: 15px;
    height: 15px;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none; }

.slick-prev {
    border-top: 2px solid #585858;
    border-left: 2px solid #585858;
    position: absolute;
    top: 50%;
    transform: rotate(-45deg) translateY(-50%); }

.slick-next {
    border-bottom: 2px solid #585858;
    border-right: 2px solid #585858;
    transform: rotate(-45deg);
    position: absolute;
    top: 50%;
    transform: rotate(-45deg) translate(5px, calc(-50% + 5px)); }

.slick-prev:before,
.slick-next:before {
    content: ""; }

.slick-list {
    position: relative;
    display: block;
    overflow: hidden; }

.slick-list.draggable {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); }

.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    margin-left: auto;
    margin-right: auto; }

.slick-track:before,
.slick-track:after {
    display: table;
    content: ""; }

.slick-track:after {
    clear: both; }

.slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px; }

.slick-slide img {
    display: block; }

.slick-slide.slick-loading img {
    display: none; }

.slick-slide.dragging img {
    pointer-events: none; }

.slick-initialized .slick-slide {
    display: block; }

.slick-loading .slick-slide {
    visibility: hidden; }

.slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
    display: none; }

